
import React from "react"
import {Card, Button} from "semantic-ui-react"

import {Link} from "gatsby"

const Project_card = ({projects}) => (
    <Card.Group>
        {projects.map((project) => {
        return (
            <Card key={project.node.id}>
                <Card.Content>
                    <Card.Header>{project.node.title}</Card.Header>
                    <Card.Meta>{project.node.meta}</Card.Meta>
                    <Card.Description>
                       {project.node.desc}
                    </Card.Description>
                </Card.Content>
                <Card.Content extra>
                    <div className='ui two buttons'>
                    <Button basic color='green'>
                        <Link 
                            to={project.node.path}
                            state={{ page_name: project.node.title }}
                            >
                             Go To Dashboard</Link>
                       
                    </Button>
                    </div>
                </Card.Content>
            </Card>
        )
      })}
        
    </Card.Group>
)



export default Project_card
